import React from "react";

const Text = props => {

    return (
        <div className="text-right container my-4 py-4">
            <h2 className="display-4 text-secondary">{props.title}</h2>
            <p className="mt-4 pt-4" style={{fontSize: "17px"}}>{props.body}</p>
        </div>
    )
};

export default Text;