import React from "react";
import "./Article.css";

const Article = props => {

    return (
        <div className="main-article">
            <h3>
                {props.article.heading.dark}
                &nbsp;&nbsp;
                <span className="text-warning">{props.article.heading.warning}</span>
            </h3>
            <div className="text-center d-flex justify-content-center container">
                <p>
                    {props.article.body}
                </p>
            </div>
        </div>
    )
};

export default Article;