import React from "react";
import "./list.css";
import "./list.css"
import { Link } from "react-router-dom";

const List = props => {

    return (
        <div className="d-flex justify-content-center mt-4 div-list flex-wrap list">
            {
                props.List.map((el, key) => {
                    return (
                        <Link to={el.link} key={key} className={props.List.length !== key + 1 ? "border-footer d-link text-light text-center" : "d-link text-light text-center"}>
                            {el.text}
                        </Link>
                    )
                })
            }
        </div>
    )
};

export default List;