import React from "react";
import "./social.css";

const Social = props => {

    return (
        <div className="pb-4 d-flex justify-content-center">
            <div className="d-flex">
                {
                    props.social.map((el, key) => (
                        <a href={el.link} target={"_blank"} className="rounded-circle text-light p-3 social mx-2 d-flex justify-content-center align-items-center" key={key}>
                            <i className={el.icon}></i>
                        </a>
                    ))
                }
            </div>
        </div>
    )
};

export default Social;