import React, {useContext, useState} from "react";
import {Context} from "../../../context/Context";
import {Button, Fab,} from "@material-ui/core";
import {Collapse, Modal, ModalBody, ModalFooter} from "reactstrap";

const AddPizza = props => {
    const context = useContext(Context);
    const [lengthKala, setLengthKala] = useState(1);
    const [addErr, setAddErr] = useState(false);
    const [delErr, setDelErr] = useState(false);
    const [onClicked, setOneClicked] = useState(true);
    const [addSuccess, setAddSuccess] = useState();


    if(!props.hideModal) {
        setAddSuccess(false)
    };

    const add = () => {
        if (lengthKala < 30) {
            setLengthKala(preveState => preveState + 1)
        } else {
            setAddErr(true);
            setTimeout(() => setAddErr(false), 2500)
        }
    };
    const del = () => {
        if (lengthKala > 1) {
            setLengthKala(preveState => preveState - 1)
        } else {
            setDelErr(true);
            setTimeout(() => setDelErr(false), 2500)
        }
    };


    const btnSuccessStore = () => {
        if (onClicked) {
            setAddSuccess(true);
            setOneClicked(false);
            context.setState(preveState => ({
                ...preveState,
                navbar: {
                    ...preveState.navbar,
                    store: {
                        ...preveState.navbar.store,
                        price: preveState.navbar.store.price + props.morePizza.price * lengthKala,
                        order: preveState.navbar.store.order + lengthKala,
                    }
                }
            }));
            let storage = JSON.parse(localStorage.getItem("order-pizza"));
            if (storage !== null) {
                let runCode = true;
                for(let key = 0; key < storage.length; key++){
                    const el = storage[key];
                    if (el.title === props.morePizza.title) {
                        runCode = false;
                        storage.push({
                            ...props.morePizza,
                            order: storage[key].order + lengthKala
                        });
                        storage.splice(key, 1);
                        break;
                    }
                }
                if (runCode) {
                    storage.push({
                        ...props.morePizza,
                        order: lengthKala
                    });
                }
                localStorage.setItem("order-pizza", JSON.stringify(storage));
            } else {
                storage = [];
                storage.push({
                    ...props.morePizza,
                    order: lengthKala
                });
                localStorage.setItem("order-pizza", JSON.stringify(storage));
            }
            setTimeout(() => {
                props.hideModal();
            }, 2000)
        }
    };


    return (
        <Modal isOpen={props.showModal}>
            <div className="modal-header rtl d-flex justify-content-between align-items-center">
                <h5 className="modal-title">تعداد پیتزا</h5>
                <i className="fa fa-times text-dark cursor-pointer fa-2x" aria-hidden="true"
                   onClick={props.hideModal}></i>
            </div>
            <ModalBody>
                <Collapse isOpen={addErr}>
                    <div className="alert alert-danger text-center">بیش از حد مجاز</div>
                </Collapse>
                <Collapse isOpen={addSuccess}>
                    <div className="alert alert-success text-center">سفارش شما در سبد خرید ثبت شد</div>
                </Collapse>
                <Collapse isOpen={delErr}>
                    <div className="alert alert-danger text-center">مقداری برای حذف کردن وجود ندارد</div>
                </Collapse>

                <div className="d-flex justify-content-around mb-3">
                    <Fab onClick={del} color="secondary" className="text-light">-</Fab>
                    <h4>
                        {lengthKala}
                    </h4>
                    <Fab onClick={add} className="bg-success text-light">+</Fab>
                </div>
            </ModalBody>
            <ModalFooter className="d-flex justify-content-center">
                <Button variant="outlined" onClick={btnSuccessStore}>
                    ثبت سفارش
                </Button>
            </ModalFooter>
        </Modal>
    )
};

export default AddPizza;