import React, { useEffect, useContext } from "react";
import "./Store.css";
import Hidden from "@material-ui/core/Hidden";
import { Context } from "../../../context/Context";
import OrderMenu from "./../../order-menu/order-menu";

const Store = props => {
  const context = useContext(Context);
  useEffect(() => {
    if (localStorage.getItem("order-pizza") !== null) {
      const storage = JSON.parse(localStorage.getItem("order-pizza"));
      let price = 0;
      const order = storage.length;
      storage.forEach(el => {
        price += el.price;
      });
      context.setState(preveState => ({
        ...preveState,
        navbar: {
          ...preveState.navbar,
          store: {
            ...preveState.navbar.store,
            price: price,
            order: order
          }
        }
      }));
    }
  }, []);

  const clicked = e => {
    if (window.location.pathname === "/buy-pizza") {
      e.preventDefault();
      context.setState(preveState => ({
        ...preveState,
        navbar: {
          ...preveState.navbar,
          store: {
            ...preveState.navbar.store,
            isOpenMenu: true
          }
        }
      }));
    }
  };

  return (
    <div>
      <Hidden mdDown>
        <div className="store bg-danger">
          <a href="/order" className="text-light" onClick={e => clicked(e)}>
            <i className="fa fa-shopping-cart" aria-hidden="true"></i>
            &nbsp;&nbsp;&nbsp;
            {props.store.text}
          </a>
          <div className="paper-card">
            <div className="d-flex justify-content-between">
              <span>
                {props.store.price}
                &nbsp;
                {props.store.priceText}
              </span>
              <span>
                {props.store.orderText}: &nbsp;
                {props.store.order}
              </span>
            </div>
          </div>
        </div>
      </Hidden>
      <OrderMenu />
    </div>
  );
};

export default Store;
