import React from "react";
import { Context } from "../../context/Context";
import Social from "./social/social";
import Logo from "./logo/logo";
import List from "./list/list";
import CopyRight from "./copyright/copyRight";
import Author from "./author/author";
import Location from "./location/location";

const Footer = () => {

    return (
        <Context.Consumer>
            {
                value => (
                    <div className="bg-dark pt-4">
                        <Social social={value.state.footer.social}/>
                        <Logo logo={value.state.footer.logo}/>
                        <List List={value.state.footer.list}/>
                        <CopyRight text={value.state.footer.copyright}/>
                        <Author Author={value.state.footer.author}/>
                        <Location Location={value.state.footer.Location}/>
                    </div>
                )
            }
        </Context.Consumer>
    )
};

export default Footer;