import React from 'react';
import "./Navbar.css";
import {Context} from "../../context/Context";
import Logo from "./logo/Logo";
import Menu from "./menu/Menu";
import Address from "./address/Address";
import Store from "./store/Store";

const Navbar = () => {
    return (
        <Context.Consumer>
            {
                value => (
                    <nav className="bg-navbar bg-dark">
                        <div className="container d-flex justify-content-around align-items-center">
                            <Logo img={value.state.navbar.Logo}/>
                            <Menu navbar={value.state.navbar.menu}/>
                            <Address
                                textWarning={value.state.navbar.phone.label.textWarning}
                                textLight={value.state.navbar.phone.label.textLight}
                                phone={value.state.navbar.phone.phone}
                            />
                            <Store
                                store={value.state.navbar.store}
                            />
                        </div>
                    </nav>
                )
            }
        </Context.Consumer>
    );
};

export default Navbar;