import React from "react";
import Img from "./img/img";
import Text from "./text/text";
import { Context } from "../../context/Context";

const About = props => {

    return (
        <Context.Consumer>
            {
                value => (
                    <div>
                        <Img url={value.state.about.url}/>
                        <Text title={value.state.about.title} body={value.state.about.body}/>
                    </div>
                )
            }
        </Context.Consumer>
    )
};

export default About;