import React from "react";
import Cards from "./cards/cards";
import Article from "./article/Article";
import {Context} from "../../context/Context";
import GetNews from "./getNews/GetNews";

const Main = () => {

    return (
        <Context.Consumer>
            {
                value => (
                    <main>
                        <Cards
                            cards={value.state.main.cards}
                        />
                        <Article
                            article={value.state.main.article}
                        />
                        <GetNews inGetNews={value.state.main.inGetNews} setInGetNews={() => value.setState(preveState => ({
                            ...preveState,
                            main: {
                                ...preveState.main,
                                inGetNews: true
                            }
                        }))}/>
                    </main>
                )
            }
        </Context.Consumer>
    )
};

export default Main;