import React from "react";
import "./card.css";

const Card = props => {

    return (
        <div className="mcard">
            <div className="mcard-header">
                <i className={props.icon}></i>
                <h4>{props.title}</h4>
            </div>
            <p className="text-center mcard-body">
                {props.body}
            </p>
        </div>
    )
};

export default Card;