import React from "react";
import "./Btn.css";
import { Link } from "react-router-dom";

const Btn = props => {

    return (
        <div className="d-flex justify-content-around fullWidth container header-btn">
            {
                props.btn.map((el,key) => (
                    <div key={key}>
                        {
                            el.isHome ? (
                                <Link to={el.link} className="btn btn-outline-light btn-lg">{el.text}</Link>
                            ) : (
                                <a href={el.link} target={"_blank"} className="btn btn-outline-light btn-lg">{el.text}</a>
                            )
                        }
                    </div>
                ))
            }
        </div>
    )
};

export default Btn;