import React from "react";
import "./Address.css";
import Hidden from "@material-ui/core/Hidden";

const Address = props => (
    <Hidden mdDown>
        <div className="text-light text-center">
            <p className="">
            <span className="text-warning">
                {props.textWarning}
            </span> {props.textLight}
            </p>
            <a href={`tel://${props.phone}`} className="text-light">
                {props.phone}
            </a>
        </div>
    </Hidden>
);

export default Address;