import React, { useContext } from "react";
import { Drawer } from "@material-ui/core";
import { Context } from "./../../context/Context";

const OrderMenu = () => {
  const context = useContext(Context);
  const state = context.state;
  const menu = state.navbar.store.isOpenMenu;
  const setMenu = () => {
    context.setState(preveState => ({
      ...preveState,
      navbar: {
        ...preveState.navbar,
        store: {
          ...preveState.navbar.store,
          isOpenMenu: false
        }
      }
    }));
  };

  return (
    <Drawer anchor="left" open={menu} onClose={setMenu}>
      <div className="bg-danger p-4 text-right text-light" style={{fontSize: "23px"}}>
        سبد خرید شما
        <i class="fa fa-shopping-cart pl-2"></i>
      </div>
    </Drawer>
  );
};

export default OrderMenu;
