import React, {useContext, useState} from "react";
import "./buyPizza.css";
import {Context} from "./../../context/Context";
import {Button, Card, Fade, Grid, Hidden} from "@material-ui/core";
import AddPizza from "./add-pizza/addPizza";

const BuyPizza = () => {
    const context = useContext(Context);
    const [active, setActive] = useState(context.state.buyPizza[0].text);
    const [isFade, setIsFade] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [morePizza, setMorePizza] = useState();
    //offsetHeight: 87px;
    //after: 25px;

    const btnScroller = (e, key) => {
        setIsFade(false);
        const headerHeight = 80;
        const afterHeight = 25;
        const cardHeight = document.querySelectorAll(".text-title")[key].offsetTop;
        window.scrollTo(0, (cardHeight - (headerHeight + afterHeight)));
    };

    window.addEventListener("scroll", () => {
        if (window.location.pathname === '/buy-pizza') {
            context.state.buyPizza.forEach((el, key) => {
                const headerHeight = 80;
                const afterHeight = 25;
                const allCard = document.querySelectorAll(".card-main")[key];
                const cardOffsetTop = allCard.offsetTop;
                const cardHeight = allCard.offsetHeight;
                const windowTop = window.visualViewport.pageTop;
                const alling = cardOffsetTop + cardHeight;
                const item = document.querySelector(".myheader").querySelectorAll(".nav-item");
                if (windowTop >= (cardOffsetTop - (headerHeight + afterHeight)) && windowTop < (alling - (headerHeight + afterHeight))) {
                    item.forEach((el) => el.classList.remove("active-bar"));
                    const txt = item[key].firstChild.textContent;
                    setActive(txt);
                    item[key].classList.add("active-bar");
                } else {
                    item[key].classList.remove("active-bar");
                }
            });
        }
    });

    return (
        <div>
            <div className="buy-pizza bg-dark myheader">
                <Hidden xsDown>
                    <ul className="nav mr-4 mb-4 py-4 rtl section-media-md">
                        {
                            context.state.buyPizza.map((el, key) => (
                                <li key={key} className="nav-item">
                                    <p href={null} className="nav-link text-light cursor-pointer"
                                       onClick={(e) => btnScroller(e, key)}>
                                        <i className={el.icon + " ml-2 icon"}></i>
                                        {el.text}
                                    </p>
                                </li>
                            ))
                        }
                    </ul>
                </Hidden>

                <Hidden smUp>
                    <div className="container py-2 rtl bg-transparent text-light cursor-pointer section-media-mobile">
                        <div className="option-active navbar" onClick={() => setIsFade(true)}>
                            {active}
                            <i className="fa fa-angle-down fa-2x" aria-hidden="true"></i>
                        </div>
                        <Fade in={isFade}>
                            <ul className="menu-active-mobile">
                                <li className="text-right">
                                    <i className="fa fa-times text-dark mr-4" aria-hidden="true"
                                       onClick={() => setIsFade(false)}></i>
                                </li>
                                {
                                    context.state.buyPizza.map((el, key) => (
                                        <li key={key} className="nav-item my-1 cursor-pointer">
                                            <p className="nav-link cursor-pointer" onClick={e => btnScroller(e, key)}>
                                                {el.text}
                                            </p>
                                        </li>
                                    ))
                                }
                            </ul>
                        </Fade>
                    </div>
                </Hidden>
            </div>

            <div className="my-4 container">
                {
                    context.state.buyPizza.map((el, key) => (
                        <div key={key} className="card-main rtl">
                            <div className="text-title my-3  text-center" key={key}>
                                <h3 className="text-text text-center rtl">
                                    <span className="pl-3 text-dangered">
                                        <i className={el.icon + " pl-1 icon text-dangered"}></i>
                                        {el.text}
                                    </span>
                                </h3>
                            </div>
                            <Grid container spacing={3}>
                                {
                                    el.item.map((ele, keys) => (
                                        <Grid item key={keys} md={4} sm={6} xs={12}>
                                            <Card className="mycard mt-3" key={keys}>
                                                <Grid container>
                                                    <Grid xs={5} sm={12} item>
                                                        <div className="card-img-top" style={{height: "300px"}}>
                                                            <img src={ele.url} alt={ele.title} title={ele.title}
                                                                 className="p-4 btn-block" style={{height: "100%"}}/>
                                                        </div>
                                                    </Grid>
                                                    <Grid xs={7} sm={12} item>
                                                        <div className="card-body text-center">
                                                            <h4 className="card-title">{ele.title}</h4>
                                                            <p className="card-text">{ele.body}</p>
                                                            <p className="card-text card-price">{ele.price} تومان</p>
                                                            <Button variant="outlined"
                                                                    className="mt-4 btn-block btn-buy"
                                                                    color="default"
                                                                    onClick={() => {
                                                                        setMorePizza({
                                                                            ...ele,
                                                                            order: 1
                                                                        });
                                                                        setShowModal(true);
                                                                    }}
                                                            >
                                                                سفارش
                                                            </Button>
                                                        </div>
                                                    </Grid>
                                                </Grid>
                                            </Card>
                                        </Grid>
                                    ))
                                }
                            </Grid>
                        </div>
                    ))
                }
            </div>
            {
                showModal ? (
                    <AddPizza
                        showModal={showModal}
                        hideModal={() => setShowModal(false)}
                        morePizza={morePizza}
                    />
                ) : null
            }
        </div>
    );
};

export default React.memo(BuyPizza);
