import React from "react";

const Author = props => (
    <p className="text-light text-center">
        {
            props.Author.text
        }
        &nbsp;&nbsp;
        <a href={props.Author.link} target={"_blank"} className="text-info" style={{textDecoration: "none"}}>{props.Author.textLink}</a>
    </p>
);

export default Author;