import React from "react";

const Logo = props => {

    return (
        <div className="mt-2 d-flex justify-content-center">
            <div className="d-flex">
                {
                    props.logo.map((el, key) => (
                        <a href={el.link} target={"_blank"} key={key}>
                            <img
                                src={el.url}
                                alt={el.alt}
                                title={el.title}
                                className="mx-3"
                            />
                        </a>
                    ))
                }
            </div>
        </div>
    )
};

export default Logo;