import React, { useState } from "react";
import "./Menu.css";
import { NavLink } from "react-router-dom";
import Hidden from "@material-ui/core/Hidden";
import PageBlack from "./../pageBlack";

const Menu = props => {
  const [toggleMenu, setToggleMenu] = useState({ right: "-100%" });
  const [pageBlack, setPageBlack] = useState({
    opacity: "0",
    zIndex: "10",
    visibility: "hidden",
  });

  const MSetShowMenu = () => {
    setToggleMenu({ right: "0" });
    setPageBlack(preveState => ({
      ...preveState,
      opacity: "1",
      visibility: "unset",
    }));
  };
  const MSetHideMenu = () => {
    setToggleMenu({ right: "-100%" });
    setPageBlack(preveState => ({
      ...preveState,
      opacity: "0",
      visibility: "hidden",
    }));
  };
  const MSetHideMenuLink = () => {
    MSetHideMenu();
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 600);
  };

  return (
    <div>
      <Hidden mdUp>
        <i
          className="fa fa-bars fa-3x text-light cursor-pointer"
          aria-hidden="true"
          onClick={MSetShowMenu}
        ></i>
      </Hidden>
      <PageBlack style={pageBlack} clicked={MSetHideMenu} />
      <ul className="nav nav-menu" style={toggleMenu}>
        {props.navbar.map((el, key) => (
          <li className="nav-item nav-item-menu" key={key}>
            {el.homePage ? (
              <NavLink
                to={el.link}
                className="nav-link nav-link-menu text-light"
                activeClassName="active-menu"
                exact={el.exact}
                onClick={MSetHideMenuLink}
              >
                {el.Label}
              </NavLink>
            ) : (
              <a
                href={el.link}
                className="nav-link nav-link-menu text-light"
                target={"_blank"}
                onClick={MSetHideMenuLink}
              >
                {el.Label}
              </a>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Menu;
