import React from "react";
import "./location.css";

const Location = props => {
    return (
        <div className="d-flex justify-content-center location">
            <a href="/">
                {props.Location}
                &nbsp;&nbsp;
                <i className="fa fa-location-arrow text-light fa-2x" aria-hidden="true"></i>
            </a>
        </div>
    )
};

export default Location;