import React from "react";
import Card from "./card/card";

const Cards = props => {

    return (
        <div className="d-flex flex-wrap justify-content-center py-4 container">
            {
                props.cards.map((el,key) => {
                    return (
                        <Card
                            key={key}
                            icon={el.icon}
                            title={el.title}
                            body={el.body}
                        />
                    )
                })
            }
        </div>
    )
};

export default Cards;