import React  from 'react';
import {
    Grid
} from "@material-ui/core";
import {
    Collapse
} from "reactstrap";

const GetNews = props => {
    const formSubmit = (e) => {
        e.preventDefault();
        props.setInGetNews();
        alert("تبریک \n شما در خبرنامه فست فود وای فای ثبت نام شدید.")
    };
    return (
        <Collapse isOpen={!props.inGetNews}>
            <form className="jumbotron alert-warning mb-0 text-center" onSubmit={formSubmit}>
                    <Grid container spacing={2}>
                        <Grid item md={6} xs={12}>
                            <input required placeholder="لطفا نام خانوادگی خود را وارد نمایید" className="btn-block form-control rtl"/>
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <input required type="email" placeholder="لطفا ایمیل خود را وارد نمایید" className="btn-block form-control rtl"/>
                        </Grid>
                    </Grid>
                    <button type="submit" className="btn btn-warning mt-4">ثبت نام در خبرنامه فست فود وای فای</button>
            </form>
        </Collapse>
    );
};

export default GetNews
